@font-face {
  font-family: "EBGaramond-Regular";
  src: url("./fonts/EBGaramond-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Oswald-Regular";
  src: url("./fonts/Oswald-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

html {
  font-family: "Roboto", "sans-serif";
  text-align: center; }

body {
  margin: 0; }

header {
  background-color: #000;
  height: 80px;
  padding: 20px;
  color:  #f2f2f2; }
  header h1 {
    font-size: 35px;
    line-height: 0.25px;
  }
    /* @media only screen and (max-width: 800px) {
      header h1 {
        margin-top: 25px; } }
    @media only screen and (max-width: 415px) {
      header h1 {
        font-size: 20px; } } */

header input {
        border: 0px solid #000000;
        padding: 8px;
        width: 200px;
    height: 14px;
    padding-right: 50px;
}

.img {
  margin: auto;
  width: 50%; }
  @media only screen and (max-width: 800px) {
    .img {
      width: 100%; } }

footer {
  background-color: #c5c5c5;
  position: relative;
  left: 0;
  bottom: 0;
  height: 50px;
  width: 100%;
  color: #000000;
  padding-top: 25px; }


button {
  font-size: 14px;
  padding: 7px;
  text-decoration: none;
  cursor: pointer;
  border: none;
  background-color: #ffda00;
}
  @media only screen and (max-width: 800px) {
    button {
      margin-top: 20px; } }

small a {
  color: #000000; }

.news-section {
  padding: 5px;
  margin: 5px 0;
  border-radius: 20px 20px 20px 20px;
  -moz-border-radius: 20px 20px 20px 20px;
  -webkit-border-radius: 20px 20px 20px 20px;
  border: 0px solid #000000;
}

.news-section p {
  font-size: 18px;
};

.search{
  justify-content: flex-end;
  display: flex;
  margin-left: -50px;
  height: 20px;
  width: 50px;
}

.social-icon {
  width: 30px;
  margin-right: 20px;
  float: left; }
  .social-icon:hover, .social-icon:focus {
    opacity: 0.6;
    transition: ease 0.2s; }
    .scrolltop:hover, .scrolltop:focus {
      opacity: 0.6;
      transition: ease 0.2s; }
  .social-icon-div {
    width: 100%;
    padding-left: 20px; }

@media only screen and (max-width: 415px) {
  #doc {
    padding: 0 5px 0 5px; } }


